<template>
  <view class="list_item" @click="toDetail">
    <view class="left">
      <image class="image" :src="dealImage()" mode="aspectFill" />
    </view>
    <view class="right">
      <view class="row1 flex_between">
        <view class="title">{{ info.name }}</view>
      </view>
      <view class="row2" v-if="info.sport_tag_str">
        <pa-tag v-for="(tag, sindex) in info.sport_tag_str.slice(0, 2)" :key="sindex">
          {{ tag }}
        </pa-tag>
        <pa-tag v-if="info.sport_tag_str.length > 1">...</pa-tag>
      </view>
      <view class="row3 flex_between">
        <uni-icons
          type="iconfont-location-outline"
          custom-prefix="iconfont"
          size="16"
          color="#A5ADBA"
        ></uni-icons>
        <view class="distance" v-if="info.show_distance">{{ info.show_distance }}</view>
        <view class="address">{{ info.address || '暂无位置信息' }}</view>
        <!-- 		<view class="foo-right" v-if="info.service_status == 1 && info.service_provider != 0" @click="handleReserve">预约</view> -->
      </view>
    </view>
  </view>
</template>

<script>
import defaultUrl from '@/static/images/default-stadium.png'
export default {
  name: 'stadiumItem',
  emits: ['reserve', 'click'],
  props: {
    info: {
      type: Object,
      default: () => {
        return {}
      },
    },
  },
  data() {
    return {
      defaultUrl: defaultUrl,
    }
  },
  computed: {
    toDistance() {
      let item = this.info
      let distance = '位置未知'
      return distance
    },
  },
  methods: {
    dealImage() {
      let info = this.info
      let returnImg = defaultUrl
      Object.prototype.toString.call(info.images_url) === '[object Array]'
      if (Object.prototype.toString.call(info.images_url) === '[object Array]') {
        if (info.images_url.length > 0) {
          returnImg = info.images_url[0]
        } else {
          if (info.sport_tourism_id) {
            returnImg = 'https://cdn-static.papa.com.cn/yuncheng/default-tour.png'
          }
        }
      }
      return returnImg
    },
    handleReserve() {
      this.$emit('reserve', this.info)
    },
    toDetail() {
      this.$emit('click', this.info)
    },
  },
}
</script>

